@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);

.features-container{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
  margin: 1rem;
}

.feature-title{
  flex: 1 1;
  max-width: 180px;
  margin-right: 2rem;
}

.feature-title h1{ 
  font-family: Manrope;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.04em;
  color: #FFFFFF;
}

.feature-title div{
  width: 38px;
  height: 3px;
  background: var(--gradient-bar);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 0.25rem;
}

.feature-text {
  flex: 2 1;
  max-width: 390px;
  display: flex;
}

.feature-text p{
  font-family: Manrope;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #81AFDD;
}

@media screen and (max-width: 550px) {
    .feature-title h1 {
        font-size: 14px;
        line-height: 22px;
    }

    .feature-text p {
        font-size: 12px;
        line-height: 20px;
    }

    .feature {
        margin: 1rem 0;
    }
}
.features-wrapper{
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

.features{
  flex: 1 1;
}

.features .feature{
  width: 100%;
  display: flex;
  flex-direction: row;
  flex: 1 1;
}

.features-heading{
  flex: 1 1;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;

  text-align: left;
  margin-right: 10rem;
}

.features-heading h1{
  font-family: Manrope;
  font-style: normal;
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
}

.features-heading p{
  font-family: Manrope;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #FF8A71;
}

.features .feature-text{
  margin-bottom: 1rem;
}

.feature-text p{
  font-family: Manrope;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
}

@media screen and (max-width: 1000px){
  .features-wrapper{
    flex-direction: column;
  }
  .features-wrapper .features-heading{
    margin-right: 0;
    text-align: center;
    margin-bottom: 2rem;
    justify-content: center;
  }
  .features-wrapper .features-heading h1{
    padding-bottom: 1rem;
  }
  .features-wrapper .features-heading p{
    width: 100%;
  }
  .features-wrapper .feature{
    justify-content: space-around;
  }
}

@media screen and (max-width: 550px){
  .features-wrapper .feature-title{
    flex: 1.2 1;
    margin-right: 1rem;
  }
  .features-wrapper .feature-title h1{
    font-size: 16px;
  }
  .features-wrapper .feature-text p{
    font-size: 12px;
    line-height: 20px;
  }
}

.header{
  display: flex;
}

.header-content{
  flex:1 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;

  margin-right: 5rem;
}

.header-content h1{
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  letter-spacing: -0.04em;
}

.header-content p{
  font-family: var(--font-family);
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 27px;
  color: var(--color-text);

  margin-top: 1.5rem;
}

.header-content__input{
  width: 100%;
  margin: 2rem 0 1rem;

  display: flex;
}

.header-content__input input{
  border: 2px solid var(--color-footer);
  background: var(--color-footer);
  font-family: var(--font-family);
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  line-height: 27px;
  min-height: 50px;
  font-size: 20px;
  padding: 0 1rem;
  outline: none;
  color: #FFF;
  width: 100%;
  flex: 2 1;
}

.header-content__input button{
  flex: 0.6 1;
  width: 100%;
  min-height: 50px;
  font-family: var(--font-family);
  font-size: 20px;
  font-weight: bold;
  background: #FF4820;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  font-style: normal;
  font-weight: bold;
  line-height: 27px;
  color: #FFF;
  cursor: pointer;
  outline: none;
  padding: 0 1rem;
  border: none;
}

.header-content__people{
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 2rem;
}

.header-content__people img{
  width: 181.79px;
  height: 38px;
}
.header-content__people p{
  margin: 0 0 0 1rem;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 38px;
  color: #FFFFFF;
  text-align: center;
}

.header-image{
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center;

}
.header-image img{
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 1050px){
  .header{
    flex-direction: column;
  }

  .header-content {
    margin: 0 0 3rem;
  }
}

@media screen and (max-width: 650px){
  .header h1{
    font-size: 48px;
    line-height: 60px;
  }
  .header p{
    font-size: 16px;
    line-height: 24px;
  }

  .header-content {
    margin:0 0 3rem;
  }

  .header-content__people{
    flex-direction: column;
  }
  .header-content__people p{
    margin: 0;
  }
  .header-content__input input,
  .header-content__input input button{
    font-size: 16px;
    line-height: 24px;
  }
}

@media screen and (max-width: 490px){
  .header h1{
    font-size: 36px;
    line-height: 48px;
  }
  .header p{
    font-size: 14px;
    line-height: 24px;
  }
  .header-content__input input,
  .header-content__input input button{
    font-size: 12px;
    line-height: 16px;
  }
}






.possibility{
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
}

.possibility-image{
  display: flex;
  flex: 1 1;
  justify-content: center;
  align-items: center;
}
.possibility-image img{
  width: 100%;
  height: 100%;
}


.possibility-content{
  flex: 1 1;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
}

.possibility-content > *{
  max-width: 480px;
}

.possibility-content .possibility-early_acces-top{
  font-family: Manrope;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #71E5FF;
}

.possibility-content h1{
  font-family: Manrope;
  font-style: normal;
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
  margin: 1rem 0;
}

.possibility-content .possibility-text{
  font-family: Manrope;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 30px;
  color: #81AFDD;
  margin-bottom: 1rem;
}

.possibility-content .possibility-early_acces-bottom{
  font-family: Manrope;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #FF8A71;
  margin-bottom: 1rem;
}
