  -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

          filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.what-feature{
  display: flex;
}

.what-feature .feature{
  margin: 0;
  display: flex;

}

.what-feature .feature-title{
  flex: 1 1;
}

.what-feature .feature-text{
  max-width: 800px;
}

.what-heading{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 4rem 0 2rem;
}

.what-heading h1{
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
}

.what-heading p{
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
