.header{
  display: flex;
}

.header-content{
  flex:1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;

  margin-right: 5rem;
}

.header-content h1{
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  letter-spacing: -0.04em;
}

.header-content p{
  font-family: var(--font-family);
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 27px;
  color: var(--color-text);

  margin-top: 1.5rem;
}

.header-content__input{
  width: 100%;
  margin: 2rem 0 1rem;

  display: flex;
}

.header-content__input input{
  border: 2px solid var(--color-footer);
  background: var(--color-footer);
  font-family: var(--font-family);
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  line-height: 27px;
  min-height: 50px;
  font-size: 20px;
  padding: 0 1rem;
  outline: none;
  color: #FFF;
  width: 100%;
  flex: 2;
}

.header-content__input button{
  flex: 0.6;
  width: 100%;
  min-height: 50px;
  font-family: var(--font-family);
  font-size: 20px;
  font-weight: bold;
  background: #FF4820;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  font-style: normal;
  font-weight: bold;
  line-height: 27px;
  color: #FFF;
  cursor: pointer;
  outline: none;
  padding: 0 1rem;
  border: none;
}

.header-content__people{
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 2rem;
}

.header-content__people img{
  width: 181.79px;
  height: 38px;
}
.header-content__people p{
  margin: 0 0 0 1rem;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 38px;
  color: #FFFFFF;
  text-align: center;
}

.header-image{
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;

}
.header-image img{
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 1050px){
  .header{
    flex-direction: column;
  }

  .header-content {
    margin: 0 0 3rem;
  }
}

@media screen and (max-width: 650px){
  .header h1{
    font-size: 48px;
    line-height: 60px;
  }
  .header p{
    font-size: 16px;
    line-height: 24px;
  }

  .header-content {
    margin:0 0 3rem;
  }

  .header-content__people{
    flex-direction: column;
  }
  .header-content__people p{
    margin: 0;
  }
  .header-content__input input,
  .header-content__input input button{
    font-size: 16px;
    line-height: 24px;
  }
}

@media screen and (max-width: 490px){
  .header h1{
    font-size: 36px;
    line-height: 48px;
  }
  .header p{
    font-size: 14px;
    line-height: 24px;
  }
  .header-content__input input,
  .header-content__input input button{
    font-size: 12px;
    line-height: 16px;
  }
}




