.what{
  display: flex;
  flex-direction: column;

  padding: 2rem;
  background: var(--color-footer);

  /* ff 3.6+ */
  background:-moz-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

  /* safari 5.1+,chrome 10+ */
  background:-webkit-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

  /* opera 11.10+ */
  background:-o-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

  /* ie 10+ */
  background:-ms-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

  /* global 92%+ browsers support */
  background:radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.what-feature{
  display: flex;
}

.what-feature .feature{
  margin: 0;
  display: flex;

}

.what-feature .feature-title{
  flex: 1;
}

.what-feature .feature-text{
  max-width: 800px;
}

.what-heading{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 4rem 0 2rem;
}

.what-heading h1{
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
}

.what-heading p{
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: var(--color-subtext);
  cursor: pointer;
}

.features-container{
  display: flex; 
  flex-wrap: wrap;
  flex-direction: row;
  margin-top: 2rem;
}

.features-container .feature {
  flex: 1;
  margin: 1rem;
  min-width: 210px;
  display: unset;
  flex-direction: column;
}

/* Customizing feature component as per needs */
.features-container .feature-text {
  margin-top: 0.5rem;
}

@media screen and (max-width: 1000px) {
  .what-heading {
      flex-direction: column;
      align-items: flex-start;
      margin: 2rem 0;
  }

  .what-heading p {
      margin-top: 0.5rem;
  }

  .features-container{
    margin: 0;
  }
  .features-container .feature {
    margin: 1rem 0;
  }
}

@media screen and (max-width: 650px) {
  /* Customizing feature component as per needs */
  .what-feature .feature {
      flex-direction: column;
  }

  /* Customizing feature component as per needs */
  .what-feature .feature-text {
      margin-top: 0.5rem;
  }

  .what-heading h1 {
      font-size: 26px;
      line-height: 40px;
  }
}

@media screen and (max-width: 350px) {
  /* Customizing feature component as per needs */
  .features-container .feature {
      margin: 0;
      min-width: 100%;
  }
}
