.features-wrapper{
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

.features{
  flex: 1;
}

.features .feature{
  width: 100%;
  display: flex;
  flex-direction: row;
  flex: 1;
}

.features-heading{
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;

  text-align: left;
  margin-right: 10rem;
}

.features-heading h1{
  font-family: Manrope;
  font-style: normal;
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
}

.features-heading p{
  font-family: Manrope;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #FF8A71;
}

.features .feature-text{
  margin-bottom: 1rem;
}

.feature-text p{
  font-family: Manrope;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
}

@media screen and (max-width: 1000px){
  .features-wrapper{
    flex-direction: column;
  }
  .features-wrapper .features-heading{
    margin-right: 0;
    text-align: center;
    margin-bottom: 2rem;
    justify-content: center;
  }
  .features-wrapper .features-heading h1{
    padding-bottom: 1rem;
  }
  .features-wrapper .features-heading p{
    width: 100%;
  }
  .features-wrapper .feature{
    justify-content: space-around;
  }
}

@media screen and (max-width: 550px){
  .features-wrapper .feature-title{
    flex: 1.2;
    margin-right: 1rem;
  }
  .features-wrapper .feature-title h1{
    font-size: 16px;
  }
  .features-wrapper .feature-text p{
    font-size: 12px;
    line-height: 20px;
  }
}