
.possibility{
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
}

.possibility-image{
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
}
.possibility-image img{
  width: 100%;
  height: 100%;
}


.possibility-content{
  flex: 1;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
}

.possibility-content > *{
  max-width: 480px;
}

.possibility-content .possibility-early_acces-top{
  font-family: Manrope;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #71E5FF;
}

.possibility-content h1{
  font-family: Manrope;
  font-style: normal;
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
  margin: 1rem 0;
}

.possibility-content .possibility-text{
  font-family: Manrope;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 30px;
  color: #81AFDD;
  margin-bottom: 1rem;
}

.possibility-content .possibility-early_acces-bottom{
  font-family: Manrope;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #FF8A71;
  margin-bottom: 1rem;
}